import React, { Component } from 'react'
import '../../css/date.css'

export default class Chiffre extends Component {
  render(){
    const { chiffre } = this.props
    var aClass
    !isNaN(chiffre) ? aClass = 'cardChiffre' : aClass = 'tiret'
    return <li className={aClass +' font-Montserrat'}>
              {this.props.chiffre}
            </li>
  }
}



