import React, { Component } from 'react';
import {connect} from 'react-redux'
import '../css/slidingBar.css'

class NavBarHrz extends Component{

  constructor(props){
    super(props)
    this.state = { width : 0, margin: 0}
  }

  componentDidMount(){
    this.setState({ width2: this.refs.navBar.firstChild.firstChild.offsetWidth, margin2 : this.refs.navBar.firstChild.firstChild.offsetLeft - this.refs.navBar.offsetLeft })
  }

  componentWillReceiveProps(nextProps){
    if(nextProps.slidePage.page !== this.props.slidePage.page)
      this.setState({ width2 : this.refs['presItem-'+nextProps.slidePage.page].firstChild.offsetWidth, margin2 : this.refs['presItem-'+nextProps.slidePage.page].firstChild.offsetLeft - this.refs.navBar.offsetLeft })
  }

  onHover(e){
    if(e.target.tagName === 'A')
      this.setState({ width : e.target.offsetWidth, margin : e.target.offsetLeft - this.refs.navBar.offsetLeft })
    else if (e.target.tagName === 'LI')
      this.setState({ width : e.target.firstChild.offsetWidth, margin : e.target.firstChild.offsetLeft - this.refs.navBar.offsetLeft })
  }

  onLeave(e){
      this.setState({ width : this.state.width2, margin : this.state.margin2 })
  }

  onClickHandler(e, i){
    const { go } = this.props
    go(i);
    if(e.target.tagName === 'A')
      this.setState({ width : e.target.offsetWidth, margin : e.target.offsetLeft - this.refs.navBar.offsetLeft })
    else if (e.target.tagName === 'LI')
      this.setState({ width : e.target.firstChild.offsetWidth, margin : e.target.firstChild.offsetLeft - this.refs.navBar.offsetLeft })
  }

  render(){
    const { go, listMenu, slidePage } = this.props
    const doList = listMenu.map((e, i)=>{
      return <li ref={'presItem-'+i} key={i} onClick={ (e)=> { this.onClickHandler(e, i)} } className={i === slidePage.page ? 'active' : null }><a className={'font-Montserrat'} href='javascript: void(0)' >{e}</a></li>
  })

    return (
      <nav>
        <ul ref='navBar' onMouseOver={ this.onHover.bind(this) } onMouseOut={ this.onLeave.bind(this) } >
          {doList}
        <hr id='slidingLine2' style={{ 'width': this.state.width, 'marginLeft' : this.state.margin, 'marginTop' : '-3px' }} />
        </ul>
      </nav>
    )
  }
}

export default connect(
  state => ({slidePage : state.slidePage}),
  dispatch => ({
    go : (e) => { dispatch({type : 'CHANGE_PAGE', page : e }) }
  })
)(NavBarHrz)



