import React, { Component } from 'react';
import {connect} from 'react-redux'
import Date from '../Components/Date/Date'

class Events extends Component {

  render()
  {
    return(
      <div style={{ 'margin':' 5% auto 0 auto','width':'50%' }} >
        <div className='cadrePhoto'>
        </div>
        <div className='container' >
        <Date date='30-07'/>
        <h1 className='font-Montserrat fontblue'>Nom de l’event </h1>
        <h4 className='font-Montserrat '>Nom de l‘organisation</h4>
        <hr style={Style.lineHr} />
        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Eligendi non quis exercitationem culpa nesciunt nihil aut nostrum explicabo reprehenderit optio amet ab temporibus asperiores quasi cupiditate.</p>
        </div>
      </div>
    )
  }
}

const Style = {
  lineHr : {'margin':'8% 0%', 'border':'none','width':'10%','height':'0.44em','background':'#1c51FF'},
}

export default Events



