import React, { Component } from 'react';
import {connect} from 'react-redux'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import '../css/slidingBar.css'

class NavVrt extends Component{
  constructor(props){
    super(props);
    this.state = {block : 'absolute'}
  }
  componentDidMount(){
    this.refs.navrt.offsetTop
  }

  render(){
    const { currentPage, currentScroll } = this.props
    return (
      <div ref='navrt' className='NavVrt' style={ { 'width': '170px', 'height':'50px','position':this.state.block, "overflow":"hidden"} }>
        <span className='titlePage font-Montserrat bold'>{ currentPage.currentPage }</span>
        <span className='numPage font-Montserrat' style={ currentPage.id ? { 'display':'inline-block' } : { 'display':'none' } }>{ currentPage.id }</span>
      </div>
    )  
  }
}

export default connect(
  state => ({
    currentPage : state.currentPage,
    currentScroll : state.currentScroll.scroll
  }),
  dispatch => ({
    go : (e) => { dispatch({type : 'CHANGE_PAGE', page : e }) }
  })
)(NavVrt)



