import React, { Component } from 'react'
import Chiffre from './Chiffre'
import '../../css/date.css'

export default class Date extends Component {
  render(){
    const { date } = this.props
  var dateEvent = date.split("").map( (e, i) => {
      return <Chiffre chiffre={e} key={i}/>
    })
    return <ul className='container_date'>
              {dateEvent}
            </ul>
  }
}



